import React from 'react'
import PropTypes from 'prop-types'
import Img from 'gatsby-image'
import { Grid, Row, Col } from 'react-flexbox-grid'
import { Text } from 'src/components/Base'
import LocationPin from 'src/assets/images/location-pin-white.inline.svg'
import ArrowRight from 'src/assets/images/arrow-right-white.inline.svg'
import styles from './styles.module.scss'

const EventCard = ({ title, date, location, image, url, ...props }) => (
  <a href={url} className={styles.card} whileHover={{ y: -2 }} {...props}>
    <Grid>
      <Row>
        <Col md={6} className={styles.imageCol}>
          <Img className={styles.image} alt={image.alt} fluid={image.fluid} />
        </Col>
        <Col md={6} className={styles.textCol}>
          <Text as="h3" color="white" bold className={styles.title}>
            {title}
          </Text>
          <Text color="white" className={styles.date}>
            {date}
          </Text>
          <Text color="white" className={styles.location}>
            <LocationPin className={styles.pin} />
            {location}
          </Text>
          <ArrowRight className={styles.arrow} />
        </Col>
      </Row>
    </Grid>
  </a>
)

EventCard.propTypes = {
  title: PropTypes.string.isRequired,
  date: PropTypes.string,
  location: PropTypes.string,
  image: PropTypes.object,
  url: PropTypes.string,
}

EventCard.defaultProps = {
  date: 'tba',
  location: 'tba',
  image: '',
  url: '',
}

export default EventCard
