import React from 'react'
import Img from 'gatsby-image'
import { Section, Heading, Text } from 'src/components/Base'
import { RichText } from 'prismic-reactjs'
import PropTypes from 'prop-types'
import { v4 as uuid } from 'uuid'

import styles from './styles.module.scss'

const Intro = ({ heading, text, imageItems, ...props }) => {
  const renderedImages = imageItems.map((item) => {
    return (
      <Img
        key={uuid()}
        className={styles.image}
        alt={item.image.alt}
        fixed={item.image.localFile.childImageSharp.fixed}
      />
    )
  })

  return (
    <Section narrow {...props}>
      <Heading as="h2" size="lg">
        {RichText.asText(heading.raw)}
      </Heading>
      <Text>{RichText.asText(text.raw)}</Text>
      <div className={styles.images}>{renderedImages}</div>
    </Section>
  )
}

Intro.propTypes = {
  heading: PropTypes.shape({
    raw: PropTypes.array,
    text: PropTypes.string,
    html: PropTypes.string,
  }),
  text: PropTypes.shape({
    raw: PropTypes.array,
    text: PropTypes.string,
    html: PropTypes.string,
  }),
  images: PropTypes.arrayOf(
    PropTypes.shape({
      fluid: PropTypes.object,
      alt: PropTypes.string,
    })
  ),
}

export default Intro
