import React from 'react'
import { Section, Heading } from 'src/components/Base'
import EmbedContainer from 'src/components/EmbedContainer'
import { RichText } from 'prismic-reactjs'
import PropTypes from 'prop-types'
import ReactHtmlParser from 'react-html-parser'
import { v4 as uuid } from 'uuid'

import styles from './styles.module.scss'

const Video = ({ heading, videoItems, ...props }) => {
  const renderedVideos = videoItems.map((item) => {
    return (
      <div key={uuid()} className={styles.video}>
        <EmbedContainer>
          {ReactHtmlParser(item.video_embed.html)}
        </EmbedContainer>
      </div>
    )
  })

  return (
    <Section narrow {...props}>
      <Heading as="h2" size="lg">
        {RichText.asText(heading.raw)}
      </Heading>
      <div className={styles.videos}>{renderedVideos}</div>
    </Section>
  )
}

Video.propTypes = {
  heading: PropTypes.shape({
    raw: PropTypes.array,
    text: PropTypes.string,
    html: PropTypes.string,
  }),
  items: PropTypes.arrayOf(
    PropTypes.shape({
      video_embed: PropTypes.object,
    })
  ),
}

export default Video
