import React from 'react'
import Layout from 'src/components/Layout'
import Seo from 'src/components/Seo'
import { Atf, Intro, LatestEvents, MembershipPrices, Video } from 'src/sections'
import { graphql } from 'gatsby'
import { v4 as uuid } from 'uuid'

const IndexPage = ({ data }) => {
  const slices = data.allPrismicHome.edges[0].node.data.body
  const events = data.allPrismicEvent.edges

  const renderdSlices = slices.map((slice) => {
    switch (slice.slice_type) {
      case 'atf':
        return (
          <Atf
            key={uuid()}
            claim={slice.primary.claim}
            imageLeft={{
              alt: slice.primary.image_left.alt,
              fluid: slice.primary.image_left.localFile.childImageSharp.fluid,
            }}
            imageRight={{
              alt: slice.primary.image_right.alt,
              fluid: slice.primary.image_right.localFile.childImageSharp.fluid,
            }}
          />
        )

      case 'intro_section':
        return (
          <Intro
            key={uuid()}
            heading={slice.primary.heading}
            text={slice.primary.text}
            imageItems={slice.items}
          />
        )

      case 'events_section':
        return <LatestEvents heading={slice.primary.heading} events={events} />

      case 'memberships':
        return (
          <MembershipPrices
            key={uuid()}
            heading={slice.primary.heading}
            text={slice.primary.text}
            priceTableRows={slice.items}
            pdfUrl={slice.primary.download_membership_pdf.url}
          />
        )

      case 'video':
        return (
          <Video
            key={uuid()}
            heading={slice.primary.heading}
            videoItems={slice.items}
          />
        )

      default:
        return <div>Default</div>
    }
  })

  return (
    <Layout>
      <Seo title="Home" />
      {renderdSlices}
    </Layout>
  )
}

export const query = graphql`
  query HomeQuery {
    ...LatestEvents
    allPrismicHome {
      edges {
        node {
          data {
            body {
              ... on PrismicHomeBodyIntroSection {
                ...IntroSlice
              }
              ... on PrismicHomeBodyEventsSection {
                ...EventsSlice
              }
              ... on PrismicHomeBodyMemberships {
                ...MembershipSlice
              }
              ... on PrismicHomeBodyAtf {
                ...AtfSlice
              }
              ... on PrismicHomeBodyVideo {
                ...VideoSlice
              }
            }
          }
        }
      }
    }
  }
`

export default IndexPage
