import React from 'react'
import { Section, Heading } from 'src/components/Base'
import { RichText } from 'prismic-reactjs'
import PropTypes from 'prop-types'
import { v4 as uuid } from 'uuid'
import EventCard from 'src/components/EventCard'

// import styles from './styles.module.scss'

const LatestEvents = ({ heading, events, ...props }) => {
  const renderedEvents = events.map(({ node: event }) => {
    return (
      <EventCard
        key={uuid()}
        title={event.data.name.text}
        date={event.data.date.text}
        location={event.data.location.text}
        image={{
          alt: event.data.image.alt,
          fluid: event.data.image.localFile.childImageSharp.fluid,
        }}
        url={`/${event.uid}/`}
      />
    )
  })

  return (
    <Section narrow {...props}>
      <Heading as="h2" size="lg">
        {RichText.asText(heading.raw)}
      </Heading>
      {renderedEvents}
    </Section>
  )
}

// TODO: events proptypes!
LatestEvents.propTypes = {
  heading: PropTypes.shape({
    raw: PropTypes.array,
    text: PropTypes.string,
    html: PropTypes.string,
  }),
  events: PropTypes.arrayOf(
    PropTypes.shape({
      fluid: PropTypes.object,
      alt: PropTypes.string,
    })
  ),
}

export default LatestEvents
